.TodoSearch {
  display: flex;
  background: #f9fbfc;
  border-radius: 50px;
  border: 2px solid white;
  margin: 0 24px;
  height: 64px;
  width: 30vw;
  margin-left: auto;
  margin-right: auto;
  font-size: 24px;
  text-align: left;
  padding-left: 26px;
  font-family: "Montserrat";
  font-weight: 400;
  color: #1e1e1f;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  color: #a5a5a5;
  font-family: "Montserrat";
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61dafa;
}
